import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Paper,
  Typography,
  TextField,
  Button,
  Grid,
  Box,
  IconButton,
  CircularProgress,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import { generateRecipeWithAI } from '../api/recipe';
import Autocomplete from '@mui/material/Autocomplete';
import RestaurantMenuIcon from '@mui/icons-material/RestaurantMenu';

const unitOptions = ['g', '', '', 'ml', '', '', '', '', ''];

const ingredientOptions = [
  // 
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',

  // 
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',

  // 
  '', '', '', '', '', '', '', '', '', '', '', '', '',

  // 
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',

  // 
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',

  // 
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',

  // 
  '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
];

function RecipeGenerator() {
  const navigate = useNavigate();
  const [ingredients, setIngredients] = useState([{ name: '', amount: '', unit: '' }]);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState('');
  const [servings, setServings] = useState(2);

  const handleIngredientChange = (index, field, value) => {
    const newIngredients = [...ingredients];
    newIngredients[index] = {
      ...newIngredients[index],
      [field]: value,
    };
    setIngredients(newIngredients);
  };

  const addIngredient = () => {
    setIngredients([...ingredients, { name: '', amount: '', unit: '' }]);
  };

  const removeIngredient = (index) => {
    setIngredients(ingredients.filter((_, i) => i !== index));
  };

  const handleGenerate = async () => {
    setLoading(true);
    setError('');
    setResult(null);
    try {
      const names = ingredients.map(ing => ing.name).filter(Boolean);
      const res = await generateRecipeWithAI(names, servings);
      navigate(`/recipes/${res.recipe.id}`);
    } catch (e) {
      setError(e.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container maxWidth="md" sx={{ mt: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          
        </Typography>

        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            {ingredients.map((ingredient, index) => (
              <Box key={index} sx={{ display: 'flex', gap: 2, mb: 2 }}>
                <Autocomplete
                  freeSolo
                  options={ingredientOptions}
                  inputValue={ingredient.name}
                  onInputChange={(_, newValue) => handleIngredientChange(index, 'name', newValue)}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label=""
                      required
                    />
                  )}
                  sx={{ minWidth: 150 }}
                />
                <TextField
                  label=""
                  value={ingredient.amount}
                  onChange={(e) => handleIngredientChange(index, 'amount', e.target.value)}
                  required
                />
                <FormControl required sx={{ minWidth: 100 }}>
                  <InputLabel></InputLabel>
                  <Select
                    value={ingredient.unit}
                    label=""
                    onChange={(e) => handleIngredientChange(index, 'unit', e.target.value)}
                  >
                    {unitOptions.map((option) => (
                      <MenuItem key={option} value={option}>{option}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <IconButton
                  color="error"
                  onClick={() => removeIngredient(index)}
                  disabled={ingredients.length === 1}
                >
                  <DeleteIcon />
                </IconButton>
              </Box>
            ))}
            <Button
              startIcon={<AddIcon />}
              onClick={addIngredient}
              variant="outlined"
              sx={{ mt: 2 }}
            >
              
            </Button>
          </Grid>

          <Grid item xs={12}>
            <TextField
              label=""
              type="number"
              value={servings}
              onChange={(e) => setServings(Number(e.target.value))}
              required
              sx={{ mb: 1 }}
            />
          </Grid>

          <Grid item xs={12}>
            <Box sx={{ display: 'flex', gap: 2, justifyContent: 'flex-end' }}>
              <Button
                variant="outlined"
                onClick={() => navigate('/')}
              >
                
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={handleGenerate}
                disabled={loading || ingredients.some(ing => !ing.name || !ing.amount || !ing.unit)}
              >
                {loading ? <CircularProgress size={24} /> : ''}
              </Button>
            </Box>
          </Grid>
        </Grid>
        {error && <Typography color="error" sx={{ mt: 2 }}>{error}</Typography>}
        {loading && (
          <Box
            display="flex"
            flexDirection="column"
            alignItems="center"
            justifyContent="center"
            minHeight="300px"
          >
            <RestaurantMenuIcon style={{ fontSize: 60, color: '#4CAF50' }} />
            <Typography variant="h6" sx={{ mt: 2 }}>
              AI
            </Typography>
            <CircularProgress sx={{ mt: 2 }} />
          </Box>
        )}
        {result && (
          <Box sx={{ mt: 4 }}>
            <Typography variant="h5">{result.recipe.title}</Typography>
            <Typography>{result.recipe.description}</Typography>
            <pre>{result.recipe.instructions}</pre>
            <Typography variant="h6" sx={{ mt: 2 }}></Typography>
            <ul>
              {result.variations.map((v, i) => (
                <li key={i}>
                  <strong>{v.title}</strong>: {v.description}
                  <pre>{Array.isArray(v.instructions) ? v.instructions.join('\n') : v.instructions}</pre>
                </li>
              ))}
            </ul>
          </Box>
        )}
      </Paper>
    </Container>
  );
}

export default RecipeGenerator; 